// Colors
// ---------------------------------

// Default

$color-default-black:               #102654;
$color-default-white:               #ffffff;
$color-bg-footer:                   #030a1a;
$color-bg-abonement:                #f8f9fc;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #103aae;
$color-torch-red:                   #ff1553;

// Gradient

$black-to-right:                    linear-gradient(142.51deg, #d10A42 0%, #0041ca 87.33%);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-placeholder:                    "Roboto Condensed", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1199:                           1199px;
$vp-1200:                           1200px;
$vp-1239:                           1239px;
$vp-1240:                           1240px;
$vp-1367:                           1367px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
